@import '../../sass-modules/breakpoints';

.header {
  background-color: #3848F3;
  background-image: url('../../image/background.png');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;

  padding-top: 8rem;
  position: relative;

  @media screen and (min-width: $break-header) {
    padding-bottom: 9rem;
  }
}

.App__header {
  margin-bottom: 5rem;
}

/* title --- */
.header__title {
  position: absolute;
  left: -100rem;
}

/* paragraph --- */
.header__paragraph:first-of-type {
  margin: 0;
}

.header__paragraph:last-of-type {
  margin: 2rem 0;
}

/* button --- */
.header__button {
  box-sizing: border-box;
}

/* curve --- */
.header__curve {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

/* ================================
Home page 
=================================== */
.homePage.App__header {
  @media screen and (min-width: $break-header) {
    padding-bottom: 10rem;
  }
}

/* ================================
Navstyles 
=================================== */
.navstylePage.App__header {
  @media screen and (min-width: $break-xlarge) {
    margin-bottom: 10%;
  }
}

/* ================================
Shopping 
=================================== */
.shoppingPage.App__header {
  @media screen and (min-width: $break-header) {
    padding-bottom: 4rem;
  }
  @media screen and (min-width: $break-xlarge) {
    padding-bottom: 8rem;
  }
}

/* ================================
Dashboard 
=================================== */
.dashboardPage.App__header {
  @media screen and (min-width: $break-header) {
    padding-bottom: 5rem;
  }
  @media screen and (min-width: $break-xlarge) {
    padding-bottom: 8rem;
  }
}